<template>
  <el-container class="page-container">
    <el-main class="query-form">
      <el-form
          v-model="form"
          label-width="120"
          label-position="left"
      >
        <el-row :gutter="24">
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="form.id" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="form.name" />
            </el-form-item>
          </el-col>
        </el-row>

        <el-footer>
          <el-button @click="reset" icon="el-icon-refresh-left"> </el-button>
          <el-button @click="query" icon="el-icon-search"> </el-button>
        </el-footer>
      </el-form>
    </el-main>

    <el-main class="table-main">
      <el-table
          :data="table.data"
          border
          stripe
      >
        <el-table-column
            prop="id"
            label=""
        ></el-table-column>

        <el-table-column
            prop="name"
            label=""
        ></el-table-column>

        <el-table-column
            prop="startTime"
            label=""
        ></el-table-column>

        <el-table-column
            prop="endTime"
            label=""
        ></el-table-column>

        <el-table-column
            prop="invalidCount"
            label=""
        ></el-table-column>

        <el-table-column
            fixed="right"
            label=""
            width="140">
          <template slot-scope="scope">
            <span @click="view(scope.row)" class="view-btn"></span>
            <el-divider direction="vertical"/>
            <span @click="update(scope.row)" class="update-btn"></span>
            <el-divider direction="vertical"/>
            <span @click="deleteRow(scope.row)" class="delete-btn"></span>
          </template>
        </el-table-column>
      </el-table>
    </el-main>

    <el-footer>
      <el-pagination
          @size-change="(val)=>{}"
          @current-change="(val)=>{}"
          :current-page="table.page"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="table.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="table.total"
      ></el-pagination>
    </el-footer>
  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        form: {
        },

        table: {
          page: 1,
          size: 10,
          total: 0,
          data: []
        }
      }
    },

    created() {
      this.query();
    },

    methods: {
      reset() {
        this.form.id = '';
        this.form.name = '';
      },
      query() {
        let res = this.dataFor10.query(this.form, 1, this.table.size);
        this.table.total = res.total;
        this.table.data = res.records;
      },
      view(row) {
        this.$router.push({
          path: '/page10/view',
          params: {
            id: row.id
          }
        })
      },
      update(row) {
        this.$router.push({
          path: '/page10/update',
          params: {
            id: row.id
          }
        })
      },
      deleteRow(row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.dataFor10.deleteRow(row.id);
          this.$message({
            type: 'success',
            message: '!'
          });
          this.query();
        })
      },
    },
  }
</script>

